import { Component } from 'react';
import MarkdownPreview from '@uiw/react-markdown-preview';
import Code from './Code';
import styles from './index.module.less';

interface MarkdownProps { }
interface MarkdownState {
  mdStr: string;
}

const getCodeStr = (data: any[] = [], code: string = '') => {
  data.forEach((node) => {
    if (node.type === 'text') {
      code += node.value;
    } else if (node.children && Array.isArray(node.children)) {
      code += getCodeStr(node.children);
    }
  });
  return code;
};

/**
 * 
 * 
 * ```md
 * <!--DemoStart,bgWhite,noCode,noPreview,noScroll,codePen-->
 * ```
 * 
 * 
 * - `bgWhite` 
 * - `noCode`  
 * - `noPreview` 
 * - `noScroll` 
 * - `codePen`  Codepen  `` `import`  Codepen 
 */
export interface OptionsMarkdown {
  bgWhite?: boolean;
  noCode?: boolean;
  noPreview?: boolean;
  noScroll?: boolean;
  codePen?: boolean;
};

export default class Markdown extends Component<MarkdownProps, MarkdownState> {
  constructor(props:MarkdownProps) {
    super(props);
    this.state = {
      mdStr: '',
    };
  }
  getMdStr?: () => Promise<typeof import("*.md")>;
  dependencies?: any;
  componentDidMount() {
    if (this.getMdStr) {
      this.getMdStr().then((str: any) => {
        this.setState({
          mdStr: str.default || str,
        });
      });
    }
  }
  render() {
    return (
      <MarkdownPreview
        style={{ padding: '20px 26px' }}
        source={this.state.mdStr}
        className={styles.markdown}
        components={{
          /**
           * bgWhite 
           * noCode 
           * noPreview 
           * noScroll 
           * codePen  Codepen   import  Codepen 
           */
           code: ({ inline, node, ...props }) => {
            const { noPreview, noScroll, bgWhite, noCode, codePen, codeSandbox } = props as any;
            if (inline) {
              return <code {...props} />;
            }
            const config = { noPreview, noScroll, bgWhite, noCode, codePen } as any;
            if (Object.keys(config).filter((name) => config[name] !== undefined).length === 0) {
              return <code {...props} />;
            }
            return (
              <Code
                code={getCodeStr(node.children)}
                dependencies={this.dependencies}
                {...{ noPreview, noScroll, bgWhite, noCode, codePen, codeSandbox }}
              />
            );
          },
        }}
      />
    )
  }
}